* {
  margin: 0;
  padding: 0;
  font-family: "Nunito Sans";
}

html {
  background-color: #000000;
}

.App {
  display: flex;
  padding: 1cm;
  justify-content: flex-end;
  align-items: flex-start;
  max-height: calc(100vh -2cm);
  overflow: auto;
}

.time-text {
  color: rgb(238, 238, 238);
  font-size: 50px;
  font-family: "Nunito Sans";
  font-weight: 600;
}

.day-name{
  color: white;
}

.period {
  font-size: 20px;
  margin-left: .07cm;
}
.city-input-box{
  display: flex;
  margin-top: .2cm;
  align-items: center;
  gap: .2cm;
}
.city-name{
  padding: .15cm;
  border-radius: 5px;
  background-color: transparent;
  border: 1px solid rgb(86, 86, 86);
  color: white;
}
.check{
  width: .6cm;
  border-radius: 5px;
  cursor: pointer;
  border: 1px solid rgb(86, 86, 86);
  padding: .1cm;
}
.time-box {
  padding: .7cm 1cm;
  background-color: #0f0f0f;
  /* margin-top: 2cm; */
  backdrop-filter: blur(10px);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.temp-line{
  display: flex;
  margin-top: .05cm;
  gap: .3cm;
  align-items: center;
}